<template>
    <div class="my-intro" v-if="taskInfo">
        <div class="my-space-Between">
            <h4 class="my-intro-hd">{{taskInfo.title}}</h4>
            <el-button type="primary" @click="onPublish"></el-button>

        </div>
        <div class="my-intro-bd">
            <p><i></i>{{taskInfo.name}}</p>
            <p><i></i>{{taskInfo.money}}</p>
            <p><i></i>{{taskInfo.deadTime}}</p>
            <p><i></i>{{taskInfo.description}}</p>
            <p v-if="taskInfo.serviceTime"><i></i>{{taskInfo.serviceTime}}</p>
            <p v-if="taskInfo.demand"><i></i>{{taskInfo.demand}}</p>
            <p v-if="taskInfo.remark"><i></i>{{taskInfo.remark}}</p>
            <div class="my-space-Between">
                <p><i></i>{{taskInfo.address}}</p>
                <p></p>
            </div>
            <div class="info-box_tags" v-if="taskInfo.tags" >
                <el-tag style="margin-right: 10px;" color="#6992cc" v-for="item in taskInfo.tags" key>{{item.name}}</el-tag>
            </div>
            <div v-else>
                <el-tag type="gray"></el-tag>
            </div>
        </div>
        <div class="intro-warn"><span></span><span> 0 </span></div>
        <div class="btn-group">
            <el-button type="primary" @click="onAccept"></el-button>
            <el-button type="danger" @click="onWarn"></el-button>
        </div>
    </div>
</template>
<script>
  import {mapActions, mapState} from 'vuex'
  import * as IntroType from '@/store/intro/types'
  import * as GlobalType from '@/store/global/types'

  export default{
    name: 'intro',
    data(){
      return {
        taskId: this.$route.query.taskId || ''
      }
    },
    computed:{
      ...mapState({
        taskInfo:state=>state.intro.taskInfo,
        acceptStep:state=>state.intro.acceptStep,
        acceptErrorMsg:state=>state.intro.acceptErrorMsg,
        warnStep:state=>state.intro.warnStep,
        warnErrorMsg:state=>state.intro.warnErrorMsg,
      })
    },
    methods: {
      ...mapActions({
        acceptTask:IntroType.A_ACCEPT_TASK,
        warnTask:IntroType.A_WARN_TASK,
        publishShow: GlobalType.A_PUBLISH_SHOW,

      }),
      onPublish() {
        this.publishShow()
      },
      onWarn: function() {
        let self = this
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.warnTask({taskId:self.taskId}).then(()=>{
            if(self.warnStep !== 'error'){
              self.$message({
                type: 'success',
                message: '!'
              });
            }else{
              self.$message.error(self.warnErrorMsg)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      },
      onAccept(){
        let self = this

        this.$prompt('', '?', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /[\s,\S]+g/,
          inputErrorMessage: ''
        }).then(({ value }) => {
          self.acceptTask({taskId:self.taskId,value:value}).then(()=>{
            if(self.acceptStep !== 'error'){
              self.$message({
                type: 'success',
                message: '!'
              });
            }else{
              self.$message.error(self.acceptErrorMsg)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

        /*this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.acceptTask({taskId: self.taskId}).then(() => {
            if (self.acceptStep !== 'error') {
              self.$message({
                type: 'success',
                message: '!'
              });
            } else {
              self.$message.error(self.acceptErrorMsg)
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });*/
      }
    },
    mounted(){
      console.log("dfsaf",this.taskInfo)
    }
  }
</script>


